@media (max-width: 800px) {
  #home {
    height: auto; } }

.home-container {
  background: url("../../images/retro-computers.jpg") no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0.6);
  background-blend-mode: multiply;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.home-container h1 {
  color: white; }

.tagline-container h2 {
  transform: rotate(-10deg);
  font-family: 'Kristi', cursive;
  font-size: 5rem;
  font-weight: 100;
  color: whitesmoke; }

.tagline-container h2:nth-child(2) {
  text-align: center;
  font-size: 6rem;
  margin-bottom: 4rem; }

/*** Blinking Cursor on Home Page ***/
.react-rotating-text-cursor {
  animation: blink 1.1s cubic-bezier(0.68, 0.01, 0.01, 0.99) infinite; }

@keyframes blink {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }
